
.hjgreadwrap{margin-bottom:2.5rem
}
.hjgreadwrap .classic{float: left;
}
.tvtype{padding-left:.6rem;
}
.hjgreadwrap p{float: left;font-size:.4rem;width:1.6rem
}
.hjgreadwrap .txtype{float: left;font-size:.38rem;width: 9.2rem
}
.txtype span{padding:0 .25rem;color:#0063C8;display: inline-block;margin-bottom:.2rem
}
.newsitem{width:2.9rem;height:5rem;margin:.2rem .1rem;position: relative;float: left;
}
.newsitem img{width:100%;height: 4rem;
}
.newsitem span{position: absolute;left: 0;bottom:1rem;font-size:.4rem;width: 100%;height: .6rem;line-height:.6rem;text-align: center;display:block;background: rgba(0,0,0,.5);color: #fff;
}
.newsitem p.title{width:100%;text-align: center;
}



